.auth-form {
  width: 100%;
  max-width: 340px;
  min-height: 450px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 50px 30px 18px;

  background: $primary-white;
  box-shadow: 0px 0px 6px $gray-shadow;
  border-radius: 12px;

  &__content {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 40px 0 47px;
  }

  &__footer {
    width: 100%;
    margin-top: auto;
  }
}
